.page-caption {
  position: relative;
  padding-bottom: 7px;
}

.page-caption__wrapper {
  @include container-adaptive();
  @include horizontal-paddings();
  @include vertical-paddings(5px);
}

.page-caption__wrapper--image {
  position: relative;
  min-height: 389px;
  padding-top: 90px;
  text-align: center;
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center 0;
}

.page-caption__wrapper--image::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 320px;
  height: 26px;
  background-image: url("../img/bg-intro-triangle-mobile.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.page-caption--inner .page-caption__wrapper--image {
  min-height: 158px;
  padding-top: 50px;
  background-position: center bottom;
}

.page-caption__title {
  width: 260px;
  margin: 0 auto;
  padding-top: 30px;
  font-size: 18px;
  line-height: 26px;
  color: $black;
  text-align: center;
}

.page-caption__subtitle {
  width: 278px;
  margin: 0 auto;
  margin-bottom: 15px;
  margin-top: 25px;
  line-height: 25px;
  color: $grey--dark;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .page-caption::after {
    display: none;
  }

  .page-caption__wrapper {
    @include container-adaptive();
    text-align: center;
  }

  .page-caption__wrapper--image {
    min-height: 518px;
    background-image: url("../img/bg-back-tablet@1x.jpg");
  }

  .page-caption__wrapper--image::after {
    width: 768px;
    height: 57px;
    background-image: url("../img/bg-intro-triangle-tablet.svg");
  }

  .page-caption--inner .page-caption__wrapper--image {
    min-height: 290px;
  }

  .page-main__image {
    width: 460px;
    height: 350px;
  }

  .page-main__image--inner {
    margin-top: 44px;
    width: 460px;
    height: 80px;
  }

  .page-caption__title {
    margin: 0 auto;
    padding-top: 70px;
    padding-bottom: 30px;
    width: 490px;
    font-size: 21px;
  }

  .page-caption--inner .page-caption__title {
    font-size: 30px;
  }

  .page-caption__subtitle {
    margin: 0 auto;
    width: 440px;
    padding-bottom: 50px;
  }
}

@media (min-width: $desktop-width) {
  .page-caption__wrapper--image {
    background-image: url("../img/bg-back-desktop@1x.jpg");
  }

  .page-caption__wrapper--image::after {
    width: 1200px;
    background-image: url("../img/bg-intro-triangle-desktop.svg");
  }

  .page-caption--inner .page-caption__wrapper--image {
    min-height: 301px;
  }

  .page-main__image--inner {
    margin-top: 52px;
  }

  .page-caption__title {
    padding-top: 106px;
  }
}
